<template>
    <div class="shopDetail">
       <div class="topImg">
           <van-icon name="arrow-left" class="backIcon" size="1.5rem" color="#b1b1b1" @click="goBack(-1)"/>
           <van-image
                   width="100vw"
                   height="60vh"
                   fit="contain"
                   :src="product.img"
                   @click="PreviewImg(product.img)"
           />
       </div>
        <div class="productDs">
            <div class="proName">{{product.name}}</div>
            <div class="proText">{{product.desc}}</div>
            <div class="proPrice">
                <div class="realPrice">￥{{isPriceChange?selectedPrice:product.price}}</div>
                <div class="MarkingPrice" v-if="!isPriceChange"><del>￥{{product.market_price}}</del></div>
            </div>
        </div>
        <div class="hardware" v-if="product.hardWare">
            <div class="item" v-for="(v,k) in product.hardWare" :key="k">
                <img :src="v.img">
                <div class="hardwareName">{{v.name}}</div>
               <div class="hardwareDetail">{{v.detail}}</div>
            </div>
        </div>
        <div class="Options margin-top">
            <div class="cell">
                <div class="insideItem">
                <div class="cellTitle">领劵</div>
                <div class="cellContent"><span class="tag textOverflow">{{product.name}}专属 200元优惠券</span></div>
                <div class="cellIcon"><van-icon name="arrow"  size="0.5rem" color="black"/></div>
                </div>
                <div class="insideItem">
                    <div class="cellTitle">促销</div>
                    <div class="cellContent"><span class="normalTag">赠米粉卡，最高含100元话费</span></div>
                    <div class="cellIcon"><van-icon name="arrow"  size="0.5rem" color="black"/></div>
                </div>
            </div>
            <div class="cell">
                <div class="insideItem" @click="isOpenModal=true">
                    <div class="cellTitle">已选</div>
                    <div class="cellContent"><span class="normalTag">{{selectedName}}X{{skuNum}}</span></div>
                    <div class="cellIcon"><van-icon name="arrow"  size="0.5rem" color="black"/></div>
                </div>
            </div>
        </div>
        <to-top></to-top>
        <div class="bgModal" :style="{height:bgModalHeight+'px',width:bgModalWidth+'px'}" v-if="isOpenModal"></div>
        <!--选择商品模态框-->
        <transition enter-active-class="slideInUp" leave-active-class="slideOutDown"  appear >
        <div class="skuModal animated faster" v-if="isOpenModal">
            <div class="fixed" style="background-color: white;">
            <div class="topCtrl rightCenter"><van-icon name="cross"  size="0.6rem" color="black" @click="isOpenModal=false"/></div>
            <div class="skuDetail">
                 <div class="skuImg">
                     <van-image
                             class="Skuimg"
                             fit="contain"
                             :src="sku_img[0]"
                             @click="PreviewImg(sku_img[0])"/>
                 </div>
                <div class="choseSku">
                    <div class="choseSkuPrice">￥{{price}}元</div>
                    <div class="choseSkuDetail">{{choseSkuName}}</div>
                </div>
            </div>
            </div>
            <div class="srollContent">
            <div class="SkuEdition">
                <div class="Title">版本</div>
                <template v-if="product.options">
                <div class="editionCtrl optionLayout" v-for="(val,index) in product.options"  :key="index" @click="selectedOption(index,val.list)" :class="{Active:activeIndex==index}">
                    <div class="editionDetail">{{val.prop1_value}}</div>
                    <div class="editionPrice">{{val.price}}元</div>
                </div>
                </template>
            </div>
            <div class="SkuColor">
                <div class="Title">颜色</div>
                <template v-if="product.options[activeIndex].list">
                <div class="ColorCtrl optionLayout"
                     v-for="(value,index) in product.options[activeIndex].list"
                     :key="index"
                     :class="{Active:colorActive==index}"
                     @click="selectedColor(value,index)">
                    {{value.prop2_value}}
                </div>
                </template>
            </div>
            <div class="SkuNum">
                <div class="title">购买数量</div>
                <van-stepper
                        v-model="skuNum"
                        :min="1"
                        :max="20"
                        integer
                        @change="skuNumChange"
                />
            </div>
            </div>
            <div class="bottomCtrl" @click="selected">
                <div class="btn-bottom">选择</div>
            </div>
        </div>
        </transition>
        <div class="bottomNav">
           <div class="leftNav">
               <div class="leftItem" @click="goHome">
                   <van-icon name="home-o"  size="1.5rem" color="rgba(0,0,0,.54)"/>
                   <div>主页</div>
               </div>
               <div class="leftItem">
                   <van-icon name="shopping-cart-o"  size="1.5rem" color="rgba(0,0,0,.54)" :info="shopNum"  @click="toShopCar"/>
                   <div>购物车</div>
               </div>
           </div>
            <div class="rightNav">
                <div class="addShop" @click="addShop">加入购物车</div>
            </div>
        </div>
    </div>
</template>
<script>
import { Swipe, SwipeItem, Lazyload,Image,Icon,ImagePreview,Stepper,Toast} from 'vant';
import Vue from 'vue'
import  {mapState}   from 'vuex'
import {api} from '../utils/api'
import 'vant/lib/swipe/style';
import 'vant/lib/swipe-item/style';
import 'vant/lib/lazyload/style';
import 'vant/lib/image/style';
import 'vant/lib/icon/style';
import 'vant/lib/image-preview/style';
import 'vant/lib/stepper/style';
import 'vant/lib/toast/style';
Vue.use(Swipe).use(SwipeItem).use( Lazyload).use(Image).use(Icon).use(ImagePreview).use(Stepper).use(Toast);
import toTop from '../components/totop.vue'
export default{
    created(){
        this.$store.commit('render/IsHome',false);
        const pid='pid='+this.$route.query.pid;
         this.$store.dispatch("productDetail/getProduct",pid).then(()=>{
                if(this.product.options){
                    this.sku_id=this.product.options[this.activeIndex].list[this.colorActive].sku_id;
                    this.sku_img=this.product.options[this.colorActive].list[this.colorActive].imgs;
                    this.choseSkuName=this.product.options[this.activeIndex].list[this.colorActive].name;
                    this.price=this.product.options[this.activeIndex].list[this.colorActive].price;
                    this.product.options.forEach((v,k)=>{
                        v.list.forEach((v,k)=>{
                            if(v.sku_id===this.sku_id){
                                this.selectedName=v.name;
                            }
                        })
                    })
                }
            });
    },
    data(){
       return{
           NowPid:0,
           previewUrl:'',
           isOpenModal:false,
           isChosePrice:0,
           bgModalWidth:0,
           bgModalHeight:0,
           skuNum:1,
           activeIndex:0,
           colorActive:0,
           price:1499,
           sku_img:[],
           sku_id:0,
           isPriceChange:false,
           choseSkuName:'',
           selectedName:'',
           selectedPrice:1499,
           flag:0,
       }
    },
    mounted(){
        this.bgModalWidth=document.documentElement.getBoundingClientRect().width;
        this.bgModalHeight=document.documentElement.getBoundingClientRect().height;
        this.NowPid=this.$route.query.pid;
        if(this.isLogin){
            this.$store.dispatch("userMs/getShopCart");
        }
    },
    components:{
      toTop
    },
    computed:{
        ...mapState('productDetail',['product']),
        ...mapState('userMs',['ShopCartNum','isLogin']),
        shopNum(){
            return this.ShopCartNum===0?'':this.ShopCartNum;
        }
    },
    methods:{
        goHome(){
          this.$router.push('/')
        },
        PreviewImg(url){
            this.previewUrl=url;
            ImagePreview([
                this.previewUrl
            ]);
        },
        goBack(num){
            this.$router.go(num)
        },
        selectedOption(index,list){
            this.colorActive=0;
            this.activeIndex=+index;
            this.sku_img=list[this.colorActive].imgs;
            this.price=list[this.colorActive].price;
            this.choseSkuName=list[this.colorActive].name;
        },
        selectedColor(val,index){
            this.sku_img=val.imgs;
            this.colorActive=index;
            this.choseSkuName=val.name;
        },
        selected(){
            this.sku_id=this.product.options[this.activeIndex].list[this.colorActive].sku_id;
            this.isPriceChange=true;
            this.product.options.forEach((v,k)=>{
              v.list.forEach((v,k)=>{
                    if(v.sku_id===this.sku_id){
                        this.selectedName=v.name;
                        this.selectedPrice=v.price
                    }
                })
            });
            this.isOpenModal=false;
        },
        skuNumChange(){
            if(this.skuNum>20){
                Toast.fail({
                    mask: true,
                    message: '最大购买数量为20'
                });
                this.skuNum=20;
            }
            if(this.skuNum<1){
                Toast.fail({
                    mask: true,
                    message: '最小购买数量为1'
                });
                this.skuNum=1;
            }
        },
        addShop(){
            if(this.flag===0){
                this.isOpenModal=true;
            }else {
                if(this.isLogin){
                    for (let i=1;i<=this.skuNum;i++){
                        api.post(`/user/addCart`,{sku_id:this.sku_id}).then(()=>{
                            Toast.success('添加成功');
                            this.$store.dispatch("userMs/getShopCart")
                        });
                    }
                }else {
                    let toast;
                    Toast.fail({
                        duration: 500,
                        message: '请先登录'
                    });
                    const that=this;
                    const timer1=setTimeout(()=>{
                        toast=Toast.loading({
                            duration: 0,       // 持续展示 toast
                            forbidClick: true, // 禁用背景点击
                            loadingType: 'spinner',
                            message: '正在跳转'
                        });
                        clearTimeout(timer1)
                        that.$router.push({name:'mine',params:{
                            isOpenModal:true,
                            ToPid:this.NowPid
                        }})
                        toast.clear();
                    },500);
                }
            }
            this.flag++;
        },
        toShopCar(){
            this.$router.push('/shopCar')
        }
    }
}
</script>
<style lang="scss" scoped>
.shopDetail{
    background-color: white;
    height: 100%;
    padding-bottom: 64px;
    font-size: 14px;
}
.topImg{
    position: relative;
    .backIcon{
        position: absolute;
        top:25px;
        left: 10px;
        z-index: 3;
        background-color: #7a7a7a;
        border-radius: 50%;
        padding: 5px 5px;
    }
}
.productDs{
    padding: 15px 15px;
    .proName{
        font-size: 20px;
        font-weight: bold;
    }
    .proText{
        font-size: 16px;
        color: #b1b1b1;
        margin: 10px 0;
    }
    .proPrice{
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 14px;
        .realPrice{
            color: #ff6700;
            font-size: 20px;
        }
        .MarkingPrice{
            margin-left:5px ;
            color: #b1b1b1;
        }
    }

}
.hardware{
    overflow-y: auto;
    height: 80px;
    display: flex;
    flex-direction: row;
    .item{
        width: 80px;
        height: 100%;
        min-width: 80px;
        padding: 0 8px;
        position: relative;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img{
            margin: 0 auto;
            align-self: center;
            width: 24px;
            height: 24px;
        }
        .hardwareName{
            font-size: 14px;
        }
        .hardwareDetail{
           width: 80px;
           white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            align-self: flex-start;
            font-size: 12px;
        }

    }
    .item:after{
        content: '';
        position: absolute;
        right: 0px;
        top:50%;
        transform: translateY(-50%);
        border: 0.3px solid #c6c6c6;
        height: 80%;
    }
}
.Options{
    background-color: white;
    padding:0 20px ;
    .cell{
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-bottom:1px solid #c9c9c9;
        border-top:1px solid #c9c9c9;
        margin-bottom: 15px;
        .insideItem{
            position: relative;
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            height: 41px;
            border-bottom:1px solid #c9c9c9;
            box-sizing: border-box;
            padding: 0 20px;
            background-color: rgba(231, 231, 231, 0.4);
            .tag{
                display: block;
                width: 114.58px;
                border-left: 0.5px dashed red;
                border-right: 0.5px dashed red;
                border-top: 1px solid red;
                border-bottom: 1px solid red;
                color: red;
                margin-left: 20px;
                background: white;
                font-size: 14px;
            }
            .normalTag{
                display: block;
                margin-left: 20px;
                width: 200px;
                font-size: 14px;
            }
            .cellIcon{
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                right: 20px;
            }
        }
        .insideItem:last-child{
            border: 0;
        };
    }
}
.skuModal{
    width: 335px;
    padding: 0 20px 0 20px;
    position: fixed;
    z-index: 999;
    bottom: 0;
    height: 500.25px;
    background-color: white;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
    overflow-y: scroll;
   .skuDetail{
       width: 335px;
       display: flex;
       flex-direction: row;
       align-items: center;
       justify-content: flex-start;
       .Skuimg{
           width: 104.6px;
           height: 104.6px;
           border: 1px solid #e1e1e1;
       }
       .choseSku{
           display: flex;
           flex-direction: column;
           align-items: flex-start;
           margin-left: 15px;
           .choseSkuPrice{
               font-size: 22px;
               color: #ff6700;
           }
           .choseSkuDetail{
               margin-top: 5px;
               font-size: 14px;
           }
       }
   }
    .srollContent{
        margin-top: 133.5px;
        margin-bottom: 64px;
    }
    .SkuEdition{
        display: flex;
        flex-direction: column;
        .editionCtrl{
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            border-right: 0;
        }
    }
    .SkuNum{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        border-top: 1px solid #c8c9cc;
        padding: 15px 0;
    }
    .ColorCtrl{
        width: auto;
        display: inline-block;
        padding-left: 15px;
        padding-right: 15px;
        margin-right: 15px;
    }
    .bottomCtrl{
        position: fixed;
        bottom:0;
        width: 335px;
        background-color: white;
        padding: 15px 0;
        .btn-bottom{
            padding: 5px 5px;
            font-size: 16px;
            border-radius: 15px;
            background-color: #ff6700;
            color: white;
            text-align: center;
        }
    }
}
.bgModal{
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.2);
    position: absolute;
    top: 0;
}
.bottomNav{
    position: fixed;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(255,255,255,0.8);
    box-shadow: 2px 2px 10px #9c9c9c;
    border-radius: 13px;
    height: 54px;
    width: 95%;
    .leftNav{
        display: flex;
        flex-direction: row;
        width: 40%;
        align-items: center;
        height: 100%;
        text-align: center;
        .leftItem{
            display: flex;
            flex-direction: column;
            align-items: center;
            flex-grow: 1;
            font-size: 12px;
            line-height: 100%;
            color: rgba(0,0,0,.54);
            div{
                padding-top: 3px;
            }
        }
    }
    .rightNav{
        display: flex;
        flex-direction: column;
        justify-content:center;
        align-items: center;
        width: 50%;
        .addShop{
            background-color: #ff6700;
            color: white;
            font-size: 16px;
            border-radius: 20px;
            padding: 5px 15px;
        }
    }
}
/*适用class*/
.margin-top{
    margin-top: 15px;
}
    .textOverflow{
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        align-self: flex-start;
    }
    .rightCenter{
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    .leftCenter{
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    .justCenter{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .fixed{
        position: fixed;
        padding-top: 5px;
    }
.Title{
    padding: 15px 0 20px 0;
    font-size: 14px;
}
.optionLayout{
    border: 1px solid #c9c9c9;
    padding: 10px 0 10px 0;
    margin-bottom: 15px;
    font-size: 14px;
}
.Active{
    border-color:#ff6700;
    color: #ff6700;
}
    .title{
        font-size: 14px;
    }
</style>